import React from "react";
import sampleNewsletter from "../../sampleNewsletter";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBroadcastTower,
  faUserAstronaut,
  faSignOutAlt
} from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";
import "./Admin.css";
import Users from "./Users";
import Newsletters from "./Newsletters";

// To do
// build out smaller components for greater control
// populate items from state

class Admin extends React.Component {
  constructor(props) {
    super(props);
    this.state = { apiResponse: "", currentComponent: "Users" };
  }

  getComponent() {
    let component;
    switch (this.state.currentComponent) {
      case "Users":
        component = <Users />;
        break;
      case "Newsletters":
        component = <Newsletters />;
        break;
      default:
        component = <Users />;
    }
    return component;
  }
  //  callAPI() {
  //     fetch("http://localhost:9000/testAPI")
  //       .then(res => res.text())
  //       .then(res => this.setState({ apiResponse: res }));
  //   }
  getData() {
    this.setState({
      apiResponse: sampleNewsletter
    });
  }
  handleUserClick() {
    this.setState({
      currentComponent: "Users"
    });
  }
  handleNewsletterClick() {
    this.setState({
      currentComponent: "Newsletters"
    });
  }

  componentWillMount() {
    this.getData();
  }
  render() {
    return (
      <div className="homepage">
        <nav className="navbar navbar-expand-lg navbar-light bg-light py-0">
          <a className="navbar-brand" href="#">
            <FontAwesomeIcon icon={faBroadcastTower} size="1.5x" />
          </a>
          <ul class="navbar-nav ml-auto">
            <li class="nav-item">
              <a class="nav-link" href="#">
                <FontAwesomeIcon icon={faUserAstronaut} size="1x" />
                Brendan
              </a>
            </li>
            <li class="nav-item">
              <a class="nav-link" href="#">
                <FontAwesomeIcon icon={faSignOutAlt} size="1x" />
                Logout
              </a>
            </li>
          </ul>
        </nav>
        <div className="sidebar border-right py-2">
          <ul className="sidebar-nav">
            <li className="nav-title border-bottom pb-2"> Admin Menu</li>
            <li className="nav-item py-2">
              <a
                className="text-success"
                onClick={() => this.handleUserClick()}
              >
                {" "}
                Users{" "}
              </a>
            </li>
            <li className="nav-item py-2">
              <a
                className="text-success"
                onClick={() => this.handleNewsletterClick()}
              >
                {" "}
                Newsletters{" "}
              </a>
            </li>
          </ul>
        </div>
        <div className="content">
          <h5 className="py-2"> Admin Dashboard </h5>
          <div>{this.getComponent()}</div>
          <div>
            <Link to="/">Directory</Link>
          </div>
        </div>
      </div>
    );
  }
}

// {this.state.apiResponse.name}

export default Admin;
