const buildUserCards = users => (
  <div className="table-wrapper">
    <table class="table table-hover">
      <thead>
        <tr>
          <th scope="col">Name</th>
          <th scope="col">Email</th>
          <th scope="col">Status</th>
          <th scope="col">Delete</th>
        </tr>
        {users.map(user => (
          <tr class="table-light">
            <th scope="row">{user.name}</th>
            <td>{user.email}</td>
            <td>{user.admin}</td>
            <td>
              <button> X </button>
            </td>
          </tr>
        ))}
      </thead>
    </table>
  </div>
);

class Users extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      Users: [
        { name: "user1", email: "email@email", admin: "User" },
        { name: "user2", email: "email@email", admin: "Writer" },
        { name: "user3", email: "email@email", admin: "Admin" }
      ]
    };
  }
  //  callAPI() {
  //     fetch("http://localhost:9000/testAPI")
  //       .then(res => res.text())
  //       .then(res => this.setState({ apiResponse: res }));
  //   }

  render() {
    return (
      <div className="users">
        <div className="users-header">Users</div>
        <div> {buildUserCards(this.state.Users)} </div>
      </div>
    );
  }
}

